// Стили для Wordpress

.wp-block-button__link {
  font-size: rem(18);
  font-weight: 500;
  padding: rem(21) rem(51);
  cursor: pointer;
  transition: background-color 0.3s ease-out;
  letter-spacing: 0.02rem;
  text-transform: uppercase;
  border: 0;
  border-radius: rem(50);

  &:hover {
    filter: saturate(0.8);
  }
}

.wp-block-button {
  margin-top: rem(37);
}

a.phone__link:visited,
a.phone__link:hover {
  color: #fff;
}

.benefit__image,
.image--cornered img {
  border-top-left-radius: rem(70);
  border-bottom-right-radius: rem(70);
}

.image--rounded {
  margin: 0;

  img {
    border-radius: 50%;
  }
}

.trainer {
  &__wrapper {
    align-items: start;

    .wp-block-media-text__content {
      position: relative;
      padding: 0;
      margin: 0 4%;

      &:before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 46px;
        content: '';
        background: url("../img/sprites/sprite.svg#quote-left") no-repeat;
      }

      p {
        position: relative;

        &:first-child {
          margin-top: 0;
        }
      }
    }
  }
}

.works {
  &__carousel .owl-item img {
    height: 100%;
    object-fit: cover;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: rem(10);
    margin-bottom: rem(30);
  }

  &__item {

    &:hover:after {
      content: '';
      box-shadow: 0 0 0 rem(500) rgba(#000000, 0.5) inset;
    }

    .work__link {
      display: block;
      height: 100%;
      position: relative;

      &:hover {
        transition: background-color 0.3s ease-out;

        .work {
          &__picture {
            position: relative;

            &:after {
              //background-color: rgba(#000000, 0.8);
              background-color: rgba(102, 66, 0, 0.9);
              position: absolute;
              content: "";
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
            }
          }

          &__inner {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            text-align: center;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 100%;
            color: #fff;
          }

          &__title {
            font-size: rem(16);
            height: auto;
          }
        }
      }
    }
  }
}

.work {
  &__inner {
    display: none;
  }

  &__picture {
    height: rem(240);
    border-radius: unset;
    border: 1px solid rgba(102, 66, 0, 0.4);
    margin-bottom: 0;
    width: auto;
    box-sizing: border-box;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
}

.span-row-2 {
  grid-row: span 2 / auto;

  picture {
    height: 100%;
    box-sizing: border-box;
  }
}

#lightcase-info #lightcase-caption {
  color: #EEEEEE !important;

  p {
    margin-top: 0;
  }
}

.wpcf7 {

  &-form {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    label {
      padding-left: rem(20);
    }
  }

  &-text {
    padding: rem(15) rem(20);
    border: none;
    border-radius: rem(30);
  }

  &-submit {
    @extend .button;
    margin-top: rem(15);
  }

  small {
    text-align: center;
    color: #6c5303;
  }

  p:last-child {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .wpcf7-validation-errors,
  .wpcf7-mail-sent-ok {
    width: 100%;
    margin-top: 0;
  }
}

.button--second {
  margin-top: rem(30);
}