.skills {
    padding-top: rem(30);
    padding-bottom: rem(10);

    &__items {
        @include row-flex();
    }

    &__item {
        @include col();
        @include size(4);
        margin-bottom: rem(20);
    }
}

.skill {
    &__title {
        overflow: hidden;
        height: rem(55);
        text-align: center;
    }

    &__image {
        display: block;
        width: 100%;
        height: 80px;
        margin-bottom: rem(24);
    }

    &__lists {
        margin-top: rem(16);
    }
}