@include carousel(works);

.works {
    padding-top: rem(30);
    padding-bottom: rem(40);
    background-color: $light-grey;
    border-top: 1px solid $border-color;

    .owl-dots {
        text-align: center;

        .owl-dot {
            width: rem(20);
            height: rem(20);
            background: #ffffff;
            border-radius: 50%;
            border: 1px solid #cccccc;
            margin-right: rem(5);
            outline: none;

            &:last-child {
                margin-right: 0;
            }

            &.active {
                background: #444444;
                box-shadow: 0 0 0 rem(5) #ffffff inset;
            }
        }
    }
}

.work {
    &__picture {
        @extend .benefit__picture;
    }

    &__title {
        @extend .skill__title;
    }
}



