.trainer {
    padding-top: rem(30);
    padding-bottom: rem(30);
    border-top: 1px solid $border-color;
    background-color: $light-grey;

    &__content {
        @include row-flex();
    }

    &__picture {
        @include col();
        @include size(3);
        //text-align: center;
    }

    &__image,
    &__source {
        width: 100%;
        height: auto;
        border-radius: 50%;
    }

    &__description {
        @include col();
        @include size(9);
        position: relative;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 46px;
            content: '';
            background: url("../img/sprites/sprite.svg#quote-left") no-repeat;
        }

        p:first-child {
            margin-top: 0;
        }
    }
}

.description__inner {
    position: relative;
    padding-bottom: rem(14);
}