.footer {
  background: #000000;

  &__info {
    padding-top: rem(20);
    padding-bottom: rem(20);
    color: #ffffff;
  }

  &__link {
    color: $yellow;
  }
}

.to-top {
  font-size: rem(20);
  font-weight: bold;
  position: fixed;
  right: rem(40);
  bottom: rem(40);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: rem(8) rem(16) rem(10) rem(18);
  color: #ffffff;
  border: 1px solid #555555;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5);
  outline: none;
  cursor: pointer;

  &:hover {
    background: rgba(0, 0, 0, 0.7);
  }
}