.tariffs {
  padding-top: rem(30);
  padding-bottom: rem(60);

  &__items {
    @include row-flex();
  }

  &__item {
    @include col();
    @include size(4);
    display: flex;
    align-items: center;
    flex-direction: column;

    &--column {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }
}

.tariff {

  &__content {
    margin-bottom: rem(24);
  }

  &__title {

    &--yellow {
      padding-right: rem(18);
      padding-left: rem(18);
      background-color: $yellow;
    }

    &--red {
      padding-right: rem(18);
      padding-left: rem(18);
      color: #ffffff;
      background-color: $red;
    }
  }

  &__price {
    text-transform: uppercase;
    font-size: rem(24);
    font-weight: 700;
  }

  &__button {
    margin-top: auto;
  }
}