@include carousel(comments);

.comments {
    padding-top: rem(30);
    padding-bottom: rem(50);
    background-color: $yellow;

    &__item {
        position: relative;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: rem(30);
            content: "";
            background: url("../img/sprites/sprite.svg#quote-left") no-repeat;
            filter: opacity(0.5);
        }
    }

    .owl-dots {
        text-align: center;

        .owl-dot {
            width: rem(20);
            height: rem(20);
            background: lighten($yellow, 20%);
            border-radius: 50%;
            border: 1px solid darken($yellow, 4%);
            margin-right: rem(5);
            outline: none;

            &:last-child {
                margin-right: 0;
            }

            &.active {
                background: darken($yellow, 30%);
                box-shadow: 0 0 0 rem(5) lighten($yellow, 20%) inset;
            }
        }
    }
}

.comment {
    &__author {
        font-weight: 700;
        text-align: right;
    }

    &__description {
        position: relative;
    }
}