.socials {
    padding-top: rem(30);
    padding-bottom: rem(50);
    background: $light-grey;
    border-top: 1px solid $border-color;

    &__items {
        @include row-flex();
        justify-content: center;
    }
}

.social {
    &__link {
        display: block;
        @include col();
    }

    &__item {
        width: rem(80);
        height: rem(80);
    }
}