html {
  height: 100%;
}

body {
  font-family: $font-family;
  font-size: 16px;
  line-height: rem(24);
  letter-spacing: 0.05rem;
  color: #000000;
}

.container {
  @include container();
  //@include sm(max-width, rem(510));
  //@include md(max-width, rem(690));
  //@include lg(max-width, rem(930));
  //@include xl(max-width, rem(1110));
  box-sizing: border-box;
  width: 100%;
}

.wrapper {
  @include row-flex();
}

h1, h2, h3, h4, h5, h6, ul {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: rem(35);
  line-height: rem(50);
  text-transform: uppercase;
}

h2 {
  font-size: rem(28);
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}

section {
  h2 {
    margin-top: rem(30);
    margin-bottom: rem(30);
  }
}

h3 {
  font-size: rem(18);
  font-weight: 500;
  line-height: rem(30);
  text-transform: uppercase;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.pens {
  li {
    position: relative;
    margin-bottom: rem(15);
    padding-left: rem(28);

    &::before {
      position: absolute;
      top: rem(5);
      left: 0;
      width: rem(18);
      height: rem(18);
      content: '';
      background: url("../img/sprites/sprite.svg#pen") no-repeat;
    }
  }
}

.accent {
  color: $yellow;
}

.button {
  font-size: rem(18);
  font-weight: 500;
  padding: rem(21) rem(51);
  cursor: pointer;
  transition: background-color 0.3s ease-out;
  letter-spacing: 0.02rem;
  text-transform: uppercase;
  color: #ffffff;
  border: 0;
  border-radius: rem(50);
  background-color: $red;

  &:hover {
    background-color: lighten($red, 5);
    color: #ffffff;
  }
}

.visually--hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

.text-center {
  text-align: center;
}

.align-items-center {
  align-items: center;
}