.doubts {
    padding-top: rem(30);
    padding-bottom: rem(60);
    background: $yellow;

    &__items {
        @include row-flex();
    }

    &__item {
        @include col();
        @include size(3);
    }
}

.doubt {
    text-align: center;

    &__position {
        padding: rem(7) rem(24);
        background: #FFD752;
        display: inline-block;
        margin-bottom: rem(20);
    }

    &__description {
        text-align: left;

        a {
            color: $red;
            font-weight: 500;
        }
    }
}