.actions {
    padding-top: rem(30);
    padding-bottom: rem(60);
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;

    &__wrapper {
        @include row-flex();
        flex-direction: column;
        align-items: center;
    }

    &__items {
        @include row-flex();
        @include shift-left(1);
        @include shift-right(1);
    }

    &__item {
        @include col();
        @include size(3);
    }

    &__button {
        margin-top: rem(30);
    }
}

.action {
    &__image {
        height: rem(60);
        width: 100%;
    }

    &__title {
        text-align: center;
    }
}