.benefits {
    padding-top: rem(30);
    padding-bottom: rem(30);

    &__items {
        @include row-flex();
    }

    &__item {
        @include col();
        @include size(4);
    }
}

.benefit {
    &__picture {
        height: rem(200);
        width: 100%;
        display: block;
        margin-bottom: rem(20);
        overflow: hidden;
        border-top-left-radius: rem(70);
        border-bottom-right-radius: rem(70);
    }

    &__source,
    &__image {
        width: inherit;
        height: inherit;
    }
    
    &__title {
        text-align: center;
    }

    &__items {
        margin-top: rem(16);
    }
}