.video {
    //padding-top: rem(60);
    //padding-bottom: rem(60);

    &__thumb {
        @include col();
        @include size(10);
        @include shift-left(1);
        position: relative;
        overflow: hidden;
        height: 0;
        padding: 56.25% 0 0 0; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
        text-align: center;
    }

    &__frame {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}