.lessons {
  padding-top: rem(30);
  padding-bottom: rem(60);
  background: $yellow;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__items {
    @include row-flex();
  }

  &__item {
    @include col();
    @include size(6);
    position: relative;

    .ui-accordion-header-icon {
      position: absolute;
      top: rem(5);
      left: 25%;
      width: rem(22);
      height: rem(16);
      content: '';
      background: url("../img/sprites/sprite.svg#arrow") no-repeat;
    }

    .ui-accordion-header-active .ui-accordion-header-icon {
      transform: rotate(180deg);
      background: url("../img/sprites/sprite.svg#arrow") no-repeat;
    }

    &::before {
      position: absolute;
      width: rem(47);
      height: rem(47);
      content: '';
      background-image: url("../img/sprites/sprite.svg#surface");
      background-repeat: no-repeat;
    }

    &:hover,
    .ui-accordion-header-active {
      &::before {
        filter: opacity(70%);
      }
    }
  }

  &__button {
    margin-top: rem(60);
  }
}

.lesson {

  &__info {
    padding-left: rem(60);
    outline: none;

    &:hover {
      cursor: pointer;
    }
  }

  &__title {
    text-transform: uppercase;
  }

  &__description {
    margin-top: rem(5);
  }

  &__inner {
    @include row-flex();
    display: none;
    flex-wrap: nowrap;
    margin-right: 0;
    margin-bottom: rem(20);
    margin-left: 0;
    padding-top: rem(20);
    border-radius: rem(20);
    background-color: lighten($yellow, 10%);

    &.active {
      display: flex;
    }

    &.ui-accordion-content-active {
      display: flex !important;
    }
  }

  &__picture {
    width: rem(170);
    height: rem(124);
    @include col();
    margin-bottom: rem(10);
    flex-shrink: 0;
  }

  &__source {
    width: inherit;
    height: inherit;
  }

  &__image {
    width: inherit;
    height: inherit;
    border-top-left-radius: rem(70);
    border-bottom-right-radius: rem(70);
  }

  &__content {
    @include col();

    p:first-child {
      margin-top: 0;
    }
  }
}