.header {
  min-height: 100vh;
  background: url("../img/header.webp") no-repeat;
  background-size: cover;
  box-shadow: 0 0 0 rem(500) rgba(#000000, 0.5) inset;
  color: #ffffff;

  .no-webp & {
    min-height: 100vh;
    background: url("../img/header.jpg") no-repeat;
    background-size: cover;
    box-shadow: 0 0 0 rem(500) rgba(#000000, 0.5) inset;
  }

  // TOP PANEL
  &__panel {
    @include row-flex();
    align-items: center;
    justify-content: space-between;
    padding-top: rem(25);
  }

  &__logo {
    @include col();
  }

  &__menu {
    @include col();
    list-style: none;
  }

  &__phone {
    @include col();
  }

  &__hero {
    @include row-flex();
    align-items: center;
  }
}

.logo {
  &__link {
    display: block;
  }

  &__image {
    width: rem(90);
    height: rem(34);
  }
}

.menu {
  @include row-flex();

  &__item {
    @include col();
  }

  &__link {
    font-size: rem(12);
    font-weight: 500;
    transition: all 0.3s ease;
    text-transform: uppercase;
    color: #ffffff;

    &:visited {
      color: #fff;
    }

    &:hover, &.active, &:active {
      position: relative;
      color: $yellow;

      &::after {
        position: absolute;
        right: 0;
        bottom: -(rem(4));
        width: 90%;
        content: "";
        border-bottom: rem(1) solid;
      }
    }
  }
}

.phone {
  &__link {
    font-weight: 700;
    color: #ffffff;
  }
}

// HERO
.hero {
  padding-top: rem(154);
  padding-bottom: rem(154);

  &__heading {
    @include col();
    @include size(6);
    color: #ffffff;
  }

  &__info {
    @include col();
    @include size(5);
    @include shift-left(1);
    color: #ffffff;

    &--heading {
      font-size: rem(22);
      font-weight: 500;
      line-height: rem(36);
      text-transform: uppercase;
    }

    &--footer {
      font-size: rem(18);
      font-weight: 300;
      line-height: rem(30);
      margin-top: rem(23);
    }
  }

  &__lists {
    margin-left: rem(30);

    li {
      font-size: rem(18);
      font-weight: 300;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__list {
    font-size: rem(18);
    font-weight: 300;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.heading {
  &__button {
    margin-top: rem(37);
  }

  &--small {
    font-size: rem(30);
    font-weight: 400;
    display: block;
  }
}